<template>
      <div class="flex flex-col items-center justify-center">
        <div class="sm:rounded-sm bg-white shadow p-6 w-105 shadow-lg"><h1
          class="text-center mb-6 font-normal font-body text-2xl">Log In to Your Account </h1>
          <div class="mb-6" data-testid="auth-form">
            <div>
              <div class="flex flex-wrap justify-between"><label class="pr-2 mb-2">Email<span
                class="text-pink">*</span></label></div>
              <input class="h-10 flex-1 block w-full border border-gray-400 placeholder-gray-400 rounded-sm mb-6"
                name="email" v-model="email"/>
              <div class="flex flex-wrap justify-between"><label class="pr-2 mb-2">Password<span
                class="text-pink">*</span></label></div>
<!--              <input-->
<!--                class="h-10 flex-1 block w-full border border-gray-400 placeholder-gray-400 rounded-sm mb-6"-->
<!--                type="password" name="password" v-model="password"/>-->
              <password  @changecolor="changeIcon"></password>
              <button
                class="inline-flex items-center justify-center font-mono uppercase transition ease-in duration-200 hover:no-underline active:shadow-inner focus:outline-none rounded-sm h-10 px-4 text-sm border text-white shadow hover:shadow-hover hover:border-transparent active:border-transparent bg-black border-black hover:text-yellow-100 w-full"
                @click="login">Submit
              </button>
            </div>
            <div></div>
          </div>
          <div class="text-center"><a class="text-sm" @click="toForgot">Forgot your
            password?</a></div>
        </div>
        <div class="text-center p-6"><p class="text-white text-sm">Not yet a member? <a
          class="text-white underline hover:text-black" @click="toRegister">Sign up</a></p></div>
      </div>
</template>

<script>
import mixin from '@mixin';
import validate from '@/utils/validate';
import layer from '@/utils/layer';
import api from '@/api';
import Password from '@components/settings/password.vue';

export default {
  name: 'login',
  mixins: [mixin],
  components: {
    Password,
  },
  data() {
    return {
      email: null,
      password: null,
      errorType: [],
    };
  },
  methods: {
    changeIcon(arr) {
      this.$set(this, 'errorType', arr.type);
      this.password = arr.new_password;
    },
    login() {
      const { email, password } = this;
      const obj = {
        email, password,
      };
      const errorType = this.errorType.filter(i => i > 0);

      if (errorType.length < 4) {
        layer.tip('Please make sure the password is correct.', 'error');
        return;
      }

      this.validResult = validate.isRequiredCheck(obj);

      if (!this.validResult.valid) {
        layer.tip(this.validResult.msg, 'error');
      } else {
        api.loginApi.login(obj).then((result) => {
          if (!result.success) {
            layer.tip(result.error, 'error');
          } else {
            this.$store.state.email = email;
            layer.tip('login successfully !');
            setTimeout(() => {
              this.$router.push('/dashboard/welcome');
            }, 1000);
          }
        });
      }
    },
  },

};
</script>

<style scoped lang="less">
  .loginApi{
    .logo{
      height: 40px;
    }
  }
</style>
