<template>
  <div class="container">

    <input
      class="h-10 flex-1 block w-full border border-gray-400 placeholder-gray-400 rounded-sm mb-6"
      name="new_password" type="password"
      v-model="new_password" @keydown="changeIcon">
    <div class="w-1/2 -mt-2 relative tooltip">
    <div class="tips">
      <span class="bg-gray-400"
            :class="{'bg-pink':msgText===1 || msgText===2,'bg-orange-500':msgText===3,'bg-green-500':msgText===4}"></span>
      <span class="bg-gray-400"
            :class="{'bg-pink': msgText===2,'bg-orange-500':msgText===3,'bg-green-500':msgText===4}"></span>
      <span class="bg-gray-400"
            :class="{'bg-orange-500':msgText===3,'bg-green-500':msgText===4}"></span>
      <span class="bg-gray-400" :class="{'bg-green-500':msgText===4}"></span>
    </div>
    <div class="w-80 tooltip__body tooltip__body--right"
         style="margin-top: -3.5rem;">
      <header class="border-b border-gray-700 py-2 px-4"><span
        class="text-gray-500 text-sm">Password Strength</span>
      </header>
      <section class="p-4 text-sm">
        <ul>
          <li>
            <i class="fa fa-check-square text-green-500" v-if="type[0]"></i>
            <i class="fa fa-times-circle-o text-red-500" v-else></i>
            At least 8 characters in length
          </li>
          <li>
            <i class="fa fa-check-square text-green-500" v-if="type[1]"></i>
            <i class="fa fa-times-circle-o text-red-500" v-else></i>
            At least one numerical character
          </li>
          <li>
            <i class="fa fa-check-square text-green-500" v-if="type[2]"></i>
            <i class="fa fa-times-circle-o text-red-500" v-else></i>
            Contain upper and lower case characters
          </li>
          <li>
            <i class="fa fa-check-square text-green-500" v-if="type[3]"></i>
            <i class="fa fa-times-circle-o text-red-500" v-else></i>
            At least one special character
          </li>
        </ul>
      </section>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'password',

  data() {
    return {
      new_password: null,
      msgText: 0,
      type: [0, 0, 0, 0],
    };
  },
  methods: {
    checkStrong(sValue) {
      let modes = 0;
      if (sValue.length < 1) return modes;

      if (sValue.length >= 8) {
        this.type[0] = 1;
        modes += 1;
      } else {
        this.type[0] = 0;
      }

      if (/\d/.test(sValue)) {
        modes += 1;
        this.type[1] = 1;
      } else {
        this.type[1] = 0;
      }
      if (/[a-z]/.test(sValue) && /[A-Z]/.test(sValue)) {
        modes += 1;
        this.type[2] = 1;
      } else {
        this.type[2] = 0;
      }
      if (/\W/.test(sValue)) {
        modes += 1;
        this.type[3] = 1;
      } else {
        this.type[3] = 0;
      }

      return modes;
    },
    changeIcon() {
      this.$emit('changecolor', { type: this.type, new_password: this.new_password });
    },
  },
  watch: {
    new_password(newname) {
      this.msgText = this.checkStrong(newname);
      this.$emit('changecolor', { type: this.type, new_password: newname });
    },
  },
};
</script>

<style scoped lang="less">
  .tips {
    max-width: 450px;
    margin-top: -20px;
    span {
      display: inline-block;
      box-sizing: border-box;
      width: 25%;
      height: 8px;
      line-height: 20px;

      &:nth-child(1) {
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
      }

      &:nth-child(4) {
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
      }
    }
  }
</style>
